import React from 'react';
import PropTypes from 'prop-types';

export class Project extends React.Component {
  render() {
    return (<div className=" columns is-vcentered is-12 is-variable  " style={{ marginBottom: 20 }} >
      {this.props.left &&
      <div className="projectText column is-4 is-right has-text-right is-offset-2">
        <div className="textContent" >
          {this.props.children}
        </div>
      </div>
      }
      <div className={" column is-5 has-text-centered  " + (!this.props.left && "")} style={{ padding: "60px 0" }} >
        <img alt="" src={this.props.image} />
      </div>
      {!this.props.left &&
      <div className={"projectText column is-4 " + (this.props.left && " ")}>
        <div className="textContent" >
          {this.props.children}
        </div>
      </div>
      }
    </div>);
  }
}
Project.propTypes = {
  left: PropTypes.bool,
  image: PropTypes.string.isRequired,
  children: PropTypes.element.isRequired
}



