import React from 'react';
import { Icon, Title, SubTitle, Media } from 'reactbulma';
import Bio from './Bio';
const Hero = () => (
  <div className="section gradient has-text-white-bis " style={{ paddingBottom: 150, paddingTop: 150 }} >
    <a name="tp" style={{ display: 'block' }} />
    <div className="container" >
      <div className="columns is-vcentered" >
        <div className="column is-4 is-offset-1" >
          <div className="is-centered " >
            <Title is-link style={{ color: 'inherit' }} >Luca Costanzi</Title>
            <SubTitle style={{ color: 'inherit' }} >{'<FullstackWebDev/>'}</SubTitle>
          </div>
          <Bio />
          <div className="buttons " >
            <a className="button is-big is-link bgcolor-deepsky" href="mailto:luca.costanzi@gmail.com" >
              <Icon>
                <i className="far fa-envelope" />
              </Icon>
              <span>Contact me</span>
            </a>
            <span className="buttonSpan" >
              <a
                className="has-text-white"
                href="https://www.linkedin.com/in/luca-costanzi-40915014b/"
                rel="noopener noreferrer"
                target="_blank"
              >
                <Icon>
                  <i className="fab fa-linkedin" />
                </Icon>
                <span >Linkedin</span>
              </a>
            </span>
            <span className="buttonSpan" >
              <a
                className="has-text-white"
                href="https://github.com/lucacostanzi/"
                rel="noopener noreferrer"
                target="_blank"
              >
                <Icon>
                  <i className="fab fa-github" />
                </Icon>
                <span>Github</span>
              </a>
            </span>
          </div>
        </div>
        <div className="column is-3 is-offset-2" >
          <Media.Left>
            <figure className="image " ><img alt="coder" src="images/image2.svg" />
            </figure>
          </Media.Left>
        </div>
      </div>
    </div>
  </div>
);
export default Hero;



