import React from 'react';
import Header from './Header';
import { Timeline, TimelineEvent } from 'react-event-timeline';

const TimelineSection = () => (
  <div className="section has-bg" >
    <Header
      size="small"
      title="Timeline"
      subtitle="More than 7 years experience in the field"
    />
    <div className="container" >
      <div className="columns " style={{ marginTop: 40 }} >
        <div className="column is-4 is-offset-1" >
          <figure className="image " ><img alt="" src="images/job.svg" />
          </figure>
        </div>
        <div className="column is-4 is-offset-1" >
          <div className="content is-size-6 has-text-weight-normal" >
            <Timeline
              orientation="left"
              style={{ paddingTop: 20, fontWeight: 400 }} >
              <TimelineEvent
                title="Fullstack developer @ KASPER DIGITALE MEDIA"
                createdAt="2017"
                icon={<i className="fa fa-briefcase" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#209CEE' }}
                iconStyle={{ border: 0 }}
              >
                Front and back-end development.
              </TimelineEvent>
              <TimelineEvent
                title="Website .NET Developer Certification for Sitecore CMS 7.0"
                createdAt="2014"
                icon={<i className="fa fa-certificate" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#00D1B2' }}
              >
                Official SITECORE certification.
              </TimelineEvent>
              <TimelineEvent
                title="Leren programmeren in C# .NET 4.5 - ADO.NET"
                createdAt="2013"
                icon={<i className="fa fa-certificate" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#00D1B2' }}
              >
                Course at Syntra West.
              </TimelineEvent>
              <TimelineEvent
                title="Fullstack developer @ ITAKASPER BVBA"
                createdAt="2012"
                icon={<i className="fa fa-briefcase" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#209CEE' }}
              >
                Fullstack developer
              </TimelineEvent>
              <TimelineEvent
                title="Nederlands voor anderstaligen"
                createdAt="2012"
                icon={<i className="fa fa-certificate" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#00D1B2' }}
              >
                Levels from 1 to 8
              </TimelineEvent>
              <TimelineEvent
                title="Moved to Belgium"
                createdAt="2012"
                icon={<i className="fa fa-plane" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#FFDD57' }}
              >
                Ready for a new start
              </TimelineEvent>
              <TimelineEvent
                title="Webdeveloper @ SELENA SRL"
                createdAt="2011"
                icon={<i className="fa fa-briefcase" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#209CEE' }}
              >
                PHP developer
              </TimelineEvent>
              <TimelineEvent
                title="Universit degli studi di Perugia"
                createdAt="2010"
                icon={<i className="fa fa-university" />}
                iconColor='#fff'
                bubbleStyle={{ backgroundColor: '#00D1B2' }}
              >
                Bachelor degree in Computer Science
              </TimelineEvent>
            </Timeline>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default TimelineSection;



