import React from 'react';

const Bio = () => (
  <div className="is-size-6 content" style={{ paddingTop: 20, paddingBottom: 20 }} >
    I'm a full stack developer at Kasper Digitale Media with 7 years experience.
    In the last years I've been involved in
    many medium to large sized projects for clients ranging from
    Nissan to Partena Ziekenfonds.
    My weapon of choice has been php and its ecosystem, but in the last years,
    driven by an interest in
    new technologies, I've been moving more towards Javascript and front-end
    developing with React.
  </div>
);


export default Bio;



