import React from 'react';
import { Icon, Tag } from 'reactbulma';
import { Project } from './Project';
import Header from './Header';

const ProjectsList = () => (
  <div className="section has-bg" >
    <Header
      size="small"
      textCentered
      title="Goodies"
      subtitle="Freely available code of my personal projects."
      color="has-bg"
    />
    <div className="container is-primary" >
      <Project image="images/dynabox3.png" left >
        <h1 className="title  is-3 " style={{ marginBottom: 10 }} >Dynabox</h1>
        <p className="tags is-right" style={{ padding: 0, margin: 0 }} >
          <Tag dark >Jquery</Tag>
          <Tag info >Javascript</Tag>
          <Tag primary >Html</Tag>
          <Tag warning >Css</Tag>
        </p>
        <p className="content " >A Dynamically rendered dropdown. Keeps an object of properties for each
          option which
          enables
          easy updates to the component.
        </p>
        <p className="buttons is-right" >
          <a
            className="button is-small"
            href="https://github.com/lucacostanzi/dynabox"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fab fa-github" />
            </Icon>
            <span>Github</span>
          </a>
          <a
            className="button is-small"
            href="https://lucacostanzi.github.io/dynabox/"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fas fa-book" />
            </Icon>
            <span>Docs</span>
          </a>
        </p>
      </Project>
      <Project image="images/commandline3.png" left >
        <h1 className="title  is-3 " style={{ marginBottom: 10 }} >WebTerminal</h1>
        <p className="tags is-right" style={{ padding: 0, margin: 0 }} >
          <Tag dark >Jquery</Tag>
          <Tag primary >Html</Tag>
          <Tag warning >Css</Tag>
        </p>
        <p className="content " >A javascript based gimmicky command line tool.
          Interact with your keyboard like you would be doing on a real terminal!
        </p>
        <p className="buttons is-right" >
          <a
            className="button is-small"
            href="https://github.com/lucacostanzi/webterminal"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fab fa-github" />
            </Icon>
            <span>Github</span>
          </a>
          <a
            className="button is-small"
            href="https://lucacostanzi.github.io/webterminal/"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fas fa-terminal" />
            </Icon>
            <span>Demo</span>
          </a>
        </p>
      </Project>
      <Project image="images/website3.png" left >
        <h1 className="title  is-3 " style={{ marginBottom: 10 }} >This website!</h1>
        <p className="tags is-right" style={{ padding: 0, margin: 0 }} >
          <Tag dark >React</Tag>
          <Tag warning >Bulma</Tag>
        </p>
        <p className="content " >The single page application you are looking at! The dynamic list of
          skills
          is loaded
          from a remote
          json file.
        </p>
      </Project>
      <Project image="images/reactreduxorders3.png" left >
        <h1 className="title  is-3 " style={{ marginBottom: 10 }} >React-redux Orders</h1>
        <p className="tags is-right" style={{ padding: 0, margin: 0 }} >
          <Tag dark >React</Tag>
          <Tag info >Redux</Tag>
          <Tag primary >Material-ui</Tag>
          <Tag warning >Lodash</Tag>
        </p>
        <p className="content " >
          Single-page application to manage orders.
          The state of the application is kept on a redux store.
          The data for the orders, products, categories
          and customers is retrieved from a remote API.
          Local storage is used to mantain the state
          (try to edit and refresh the page). Eslint configuration:
          Airbnb. Designed with Material-ui
        </p>
        <p className="buttons is-right" >
          <a
            className="button is-small"
            href="https://github.com/lucacostanzi/redux-orders"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fab fa-github" />
            </Icon>
            <span>Github</span>
          </a>
          <a
            className="button is-small"
            href="https://lucacostanzi.github.io/redux-orders/"
            rel="noopener noreferrer"
            target="_blank"
          >
            <Icon small >
              <i className="fas fa-shopping-cart" />
            </Icon>
            <span>Demo</span>
          </a>
        </p>
      </Project>
    </div>
  </div>
);

export default ProjectsList;



