import React from 'react';
import { Icon } from 'reactbulma';

const Navbar = () => (
  <nav className="navbar  is-fixed-top is-transparent " style={{ marginBottom: 52, }} >
    <div className="navbar-brand" >
      <a className="navbar-item" href="#top" >
        <span className="color-deepsky is-bold" style={{ marginLeft: 10, fontWeight: 700, fontSize: 20 }} >
          <b><i className="fas fa-code" /> {'lucacostanzi'}</b>
        </span>
      </a>
    </div>
    <div className="navbar-menu" >
      <div className="navbar-start" >
        <a className="navbar-item" href="#timeline" >
          Timeline
        </a>
        <a className="navbar-item" href="#toolbag" >
          My tool bag
        </a>
        <a className="navbar-item" href="#projects" >
          Goodies
        </a>
      </div>
      <div className="navbar-end" >
        <a
          className="navbar-item"
          rel="noopener noreferrer"
          target="_blank"
          href="https://github.com/lucacostanzi/" >
          <Icon small >
            <i className="fab fa-github" />
          </Icon>
        </a>
        <a
          className="navbar-item"
          rel="noopener noreferrer"
          target="_blank"
          href="https://www.linkedin.com/in/luca-costanzi-40915014b/" >
          <Icon>
            <i className="fab fa-linkedin" />
          </Icon>
        </a>
      </div>
    </div>
  </nav>
);
export default Navbar;



