import React from 'react';
import './App.css';
import Navbar from './components/Navbar';
import Hero from './components/Hero';
import TimelineSection from './components/TimelineSection';
import Footer from './components/Footer';
import ProjectsList from './components/ProjectsList';
import Skills from './components/Skills';

const App = function () {
  return (
    <div>
      <Navbar />
      <div style={{ marginTop: 52 }} >

        <Hero />
        <a name="timeline" style={{ marginTop: -50, paddingTop: 50, display: 'block' }} />
        <TimelineSection/>
        <a name="toolbag" style={{ marginTop: -50, paddingTop: 50, display: 'block' }} />

        <Skills />
        <a name="projects" style={{ marginTop: -50, paddingTop: 50, display: 'block' }} />
        <ProjectsList/>

        <Footer/>
      </div>

    </div>
  );
}

export default App;



