import React from 'react';
import { Tag } from 'reactbulma';

const Footer = () => (
  <footer className="footer" style={{ padding: 40 }} >
    <div className="container" >
      <div className="columns" >
        <div className="column has-text-right" >
          <strong><a href="https://github.com/lucacostanzi/" >Luca Costanzi</a></strong> 2018
          <p>
              Made with:
              <Tag info style={{marginLeft:5}}>react</Tag>
              <Tag primary style={{marginLeft:5}} >bulma</Tag>
          </p>
          <p>
            Illustrations from https://undraw.co/
          </p>
        </div>
      </div>
    </div>
  </footer>
);

export default Footer;



